import { Component, OnInit } from '@angular/core';
import { MiscService } from '../misc.service';
import { LogService } from '../log.service';
import { Observable } from 'rxjs-compat';
import { FirebaseService } from '../firebase.service';
import { AngularFireDatabase } from 'angularfire2/database';


@Component({
  selector: 'app-feed-back',
  templateUrl: './feed-back.component.html',
  styleUrls: ['./feed-back.component.scss']
})
export class FeedBackComponent implements OnInit {
	asunto: string = '';
	telefono: string = '';
	pais: string = '';
	mensaje: string = '';
  stop: boolean = false;
  subtitle: string = undefined;
  title: string = undefined;
  phone: string = undefined;
  country: string = undefined;
  comment: string = undefined;
  btnSend: string = undefined;
  sent: string = undefined;
  sending: string = undefined;
  subject: string = undefined;
  texts: Observable<any> = undefined;
  language: string = undefined;

  constructor(
    private ms: MiscService,
    private ls: LogService,
    private firebaseService: FirebaseService,
    private firebaseDB: AngularFireDatabase
  ) {
    this.language = localStorage.getItem("lang");
   }

  ngOnInit() {
    this.loadLanguage(this.language);
  }

  loadLanguage(lang: string){
    this.texts = this.firebaseDB.list('/text/list_text/').valueChanges();
    this.texts.subscribe(k => { 
      this.subtitle = k.find(f => f.name == "feedback_info").description[lang];
      this.title = k.find(f => f.name == "feedback").description[lang];
      this.phone = k.find(f => f.name == "feedback_phone").description[lang];
      this.country = k.find(f => f.name == "feedback_country").description[lang];
      this.comment = k.find(f => f.name == "feedback_comment").description[lang];
      this.btnSend = k.find(f => f.name == "feedback_button_send").description[lang];
      this.subject = k.find(f => f.name == "feedback_subject").description[lang];
      this.sent = k.find(f => f.name == "feedback_sent").description[lang];
      this.sending = k.find(f => f.name == "feedback_sending").description[lang];
    });
  }

  enviarFormulario(){
    const msg = {
       asunto: this.asunto,
       telefono: this.telefono,
       pais: this.pais,
       mensaje: this.mensaje,
       user_mail: localStorage.getItem('mail')
    };
    this.stop = true;
    this.ls.emitChange(this.sending);
    this.ms.sendMail(msg).subscribe(res => {
      if(res['complete']){
        this.cleanFields();       
        this.ls.emitChange(this.sent);        
      }
      console.log(res);
      this.stop = false;
    });
  }

  cleanFields(){
    this.asunto = "";
    this.telefono = "";
    this.pais = "";
    this.mensaje = "";
  }

}



