import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/operators';
import { CookieService } from 'angular2-cookie/core';

import { Router } from '@angular/router';
import { ProfileService } from './profile.service';
import { AngularFireDatabase } from 'angularfire2/database';


const httpOptions = {
  headers: new HttpHeaders({ 
    'Content-Type': 'application/json'
  })
};

@Injectable()
export class AuthService {
  private base_url:string = 'https://us-central1-livetask-363bb.cloudfunctions.net/api/auth/'

  constructor(
  	private http: HttpClient,
  	private cookieService: CookieService,
    private router: Router,
    private ps: ProfileService,
    private firebaseDB: AngularFireDatabase) { };

  /**
   * Login user
   * @param {string} mail User email
   * @param {string} pass User password
   * @returns {Observable<any>} Returns data from user if exist
   */
  logIn(mail: string, pass: string): Observable<any>{
  	let path:string = 'login';
  	let query:string = '?mail='+mail+'&pass='+pass;
  	let url: string = this.base_url + path +query;
  	return this.http.get(url)
  		.pipe();
  }

  /**
   * Verify Token
   * @description Checks if there is any access token in cookies, if not, it redirect to login page
   */
  verifyAccessToken(){
    const accessToken = this.cookieService.get('accessToken');
    const lang = localStorage.getItem("lang");
    const mail = localStorage.getItem("mail");
    const navLang = this.getDefaultLang();

    if(accessToken == undefined){
      this.router.navigate(['login']);
    } else if (lang === undefined) {
      this.ps.getProfile(mail).subscribe(data => {
        if(data.lang === undefined) {
          data.lang = navLang;
          this.ps.updateProfile(data);
        } else {
          localStorage.setItem("lang", data.lang);
        }
      });
      
    }
  }

  /**
   * Register User
   * @param {string} mail User email
   * @param {string} pass User password
   * @param {string} name User name
   * @returns {any} Returns user data
   */
  registerUser(mail: string,pass: string,name: string): Observable<any>{
    let path:string = 'register';
    let url:string = this.base_url+path;
    const lang: string = this.getDefaultLang();
    return this.http.post<any>(url,{mail: mail,pass:pass, name: name, lang: lang},httpOptions).pipe();
  }

  /**
   * Recovery Password
   * @param {string} mail User email
   * @returns Returns if the recovery password has been sent to the `mail` registered
   */
  recoveryPassWord(mail: string): Observable<any>{
    let url:string = "https://us-central1-livetask-363bb.cloudfunctions.net/api/recovery";
    return this.http.post<any>(url,{mail: mail});
  }

  getDefaultLang(): string{
    let navLanguage = navigator.language.split("-")[0] !== undefined ? navigator.language.split("-")[0] : "en"
    let res: Observable<any> = this.firebaseDB.list('/text/list_language').valueChanges();
    res.subscribe(langList => {
      if(!(langList.find(lang => lang.code == navLanguage))) {
        navLanguage = "en";
      }
    });

    return navLanguage;
  }

  socialUserRegister(user: any) {
    let url = `${this.base_url}socialregister`;
    return this.http.post<any>(url, user);
  }

}



