import { Component, OnInit, Input } from '@angular/core';
import { Extension } from '../Models/extension';
import { ExtensionService } from '../extension.service';
import { UserExtension } from '../Models/user_extension';
import { Observable } from 'rxjs-compat';
import { AngularFireDatabase } from 'angularfire2/database';



@Component({
  selector: 'app-store-detail',
  templateUrl: './store-detail.component.html',
  styleUrls: ['./store-detail.component.scss']
})
export class StoreDetailComponent implements OnInit {
  @Input() extension: Extension;
  @Input() userExtensions: UserExtension[];
  isInstalled:boolean = true;
  prueba="asdxx";
  groups: string = undefined;
  free: string = undefined;
  install: string = undefined;
  open: string = undefined;
  images: string = undefined;
  description: string = undefined;

  texts: Observable<any> = undefined;
  language: string = undefined;
  
  constructor(
    private extensionService: ExtensionService,
    private firebaseDB: AngularFireDatabase) {
    this.language = localStorage.getItem("lang");
   }

  ngOnInit() {
  	console.log(this.extension);
    console.log(this.userExtensions);
    this.verifyInstalled();
    this.loadLanguage(this.language);
  }

  loadLanguage(lang: string){
    this.texts = this.firebaseDB.list('/text/list_text/').valueChanges();
    this.texts.subscribe(k => { 
      this.groups = k.find(f => f.name == "store_groups").description[lang];
      this.free = k.find(f => f.name == "store_free").description[lang];
      this.install = k.find(f => f.name == "store_install").description[lang];
      this.images = k.find(f => f.name == "store_images").description[lang];
      this.description = k.find(f => f.name == "store_description").description[lang];
    });
  }

  registerUser(){
  	this.extensionService.setUserExtension(this.extension).subscribe( response => {
  		console.log(response);
  	});
  }

  verifyInstalled(){
    for (const i in this.userExtensions) {
      if(this.userExtensions[i] !== null && this.userExtensions[i].id == this.extension.id){
        this.isInstalled = false;
        break;
      }
    }
  }

  unregisterUser(){
    
  }

}



