import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';

import { FolderService } from '../folder.service';
import { TaskService } from '../task.service';
import { LogService } from '../log.service';

import { Folder } from '../Models/folder';
import { Task } from '../Models/task';
import { Observable } from 'rxjs-compat';
import { AngularFireDatabase } from 'angularfire2/database';

@Component({
  selector: 'app-my-tasks',
  templateUrl: './my-tasks.component.html',
  styleUrls: ['./my-tasks.component.scss']
})
export class MyTasksComponent implements OnInit {
  title: string = undefined;
  folders: Folder[];
  tasks: Task[];
  user_mail: string = '';
  selectedTask: Task;
  selectedTaskSons: Task[];
  orderBy: number = 4;
  complete: number = 0;
  selectedType: string = "";
  sortBy: string = undefined;
  abcSort: string = undefined;
  createdSort: string = undefined;
  upgradeSort: string = undefined;
  favSort: string = undefined;
  completed: string = undefined;
  show: string = undefined;
  hide: string = undefined;
  task_created: string = undefined;
  task_sent: string = undefined;

  texts: Observable<any> = undefined;
  language: string = undefined;

  constructor(
  	private route: ActivatedRoute,
    private folderService: FolderService,
    private taskService: TaskService,
    private logService: LogService,
    private firebaseDB: AngularFireDatabase) {    
    this.user_mail = localStorage.getItem("mail");
    this.language = localStorage.getItem("lang");
  }

  ngOnInit() {
    this.getFolders();
    this.getTasks();
    this.loadLanguage(this.language);
  }

  loadLanguage(lang: string){
    this.texts = this.firebaseDB.list('/text/list_text/').valueChanges();
    this.texts.subscribe(k => { 
      this.sortBy = k.find(f => f.name == "filter_sorterby").description[lang];
      this.abcSort = k.find(f => f.name == "filter_sorterby_abc").description[lang];
      this.createdSort = k.find(f => f.name == "filter_sorterby_abc").description[lang];
      this.upgradeSort = k.find(f => f.name == "filter_sorterby_upgrade").description[lang];
      this.completed = k.find(f => f.name == "filter_completed").description[lang];
      this.favSort = k.find(f => f.name == "filter_sorterby_favorites").description[lang];
      this.hide = k.find(f => f.name == "filter_completed_hide").description[lang];
      this.show = k.find(f => f.name == "filter_completed_show").description[lang];
      this.title = k.find(f => f.name == "task").description[lang];
      this.task_created = k.find(f => f.name == "task_created").description[lang];
      this.task_sent = k.find(f => f.name == "task_sent").description[lang];
    });
  }

  getFolders():void{
    this.folderService.getFolders(this.user_mail)
      .subscribe(data => {
        this.folders = data;
      });
  }

  getTasks():void{
    this.taskService.getTasks(this.user_mail)
    .subscribe( data => {
      this.tasks = data;
    });
  }

  showTask(event):void{
    this.selectedTask = event.task;
    this.selectedType = event.type;
    this.selectedTaskSons = this.getSelectedSons(this.selectedTask.id);
    let task_list = document.getElementsByTagName("app-task-list")[0];
    if (!task_list.classList.contains('d-none')) {
      task_list.classList.add('d-none');
      task_list.classList.remove('d-flex');
    }
  }

  showTaskList(event):void{
    this.tasks = event.tasks;
    console.log(this.tasks);
  }

  getSelectedSons(task_id): Task[]{
    const sons:Task[] = [];
    for(const i in this.tasks){      
      if(this.tasks[i].parent_id == task_id){
        sons.push(this.tasks[i]);
      }
    }
    return sons;
  }

  execAction(event):void{
    switch (event.action) {
      case "closeTask":
        this.selectedTask = undefined;
        break;
      
      default:
        // code...
        break;
    }
  }

  saveNewTask(event): void{
    for(const i in event.tasks){
      if(event.type == 'new'){
        this.tasks.push(event.tasks[i]);
      }
      this.taskService.addTask(event.tasks[i]).subscribe(task_r => {
        if(task_r.dear_mail == task_r.from_mail){
          this.logService.emitChange(this.task_created);
        }else{
          this.logService.emitChange(this.task_sent);
        }        
      });
    }
  }

  changeOrderByState(event,val):void{
    var element = event.target;    
    var list = document.getElementsByClassName("mc2-label");
    Array.from(list).forEach( (elementx,index,array) => {
      var check = <Element>elementx.lastChild;
      //(check).classList.remove("d-none");
      check.classList.add("d-none");
    });
    if (element.classList.contains("mc2-label")) {
      element = element.lastChild;
    }
    this.orderBy = val;
    element.classList.remove("d-none");
  }

  showCompleted(event,val):void{
    var element = event.target
    var list = document.getElementsByClassName("mc2-label-completed");
    Array.from(list).forEach( (elementx,index,array) => {
      var check = <Element>elementx.lastChild;
      //(check).classList.remove("d-none");
      check.classList.add("d-none");
    });
    if (element.classList.contains("mc2-label-completed")) {
      element = element.lastChild;
    }
    this.complete = val;
    element.classList.remove("d-none");
  }

}



