import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { TaskService } from '../task.service';
import { FolderService } from '../folder.service';

import { Task } from '../Models/task';
import { Folder } from '../Models/folder';
import { Observable } from 'rxjs-compat';
import { FirebaseService } from '../firebase.service';
import { AngularFireDatabase } from 'angularfire2/database';

@Component({
  selector: 'app-inbox',
  templateUrl: './inbox.component.html',
  styleUrls: ['./inbox.component.scss']
})

export class InboxComponent implements OnInit {
  title:string;
  tasks: Task[];
  folders: Folder[];
  user_mail: string;
  btnAccept: string = undefined;
  btnReject: string = undefined;
  noTasks1: string = undefined;
  noTasks2: string = undefined;

  texts: Observable<any> = undefined;
  language: string = undefined;

  showSelectDialog: boolean = false;
  toselectTask: Task;
  selectTitle:string = undefined;

  constructor(
  	private route: ActivatedRoute,
  	private titleService: Title,
    private taskService: TaskService,
    private folderService: FolderService,
    private firebaseDB: AngularFireDatabase) {
    this.user_mail = localStorage.getItem("mail");
    this.language = localStorage.getItem("lang");
  }

  ngOnInit() {
  	this.titleService.setTitle(this.title);
    this.getFolders();
    this.getTasks();
    this.loadLanguage(this.language);
  }

  loadLanguage(lang: string){
    this.texts = this.firebaseDB.list('/text/list_text/').valueChanges();
    this.texts.subscribe(k => { 
      this.btnAccept = k.find(f => f.name == "inbox_taskslide_accept").description[lang];
      this.btnReject = k.find(f => f.name == "inbox_taskslide_reject").description[lang];
      let noTaskaux = k.find(f => f.name == "inbox_info").description[lang];
      this.noTasks1 = noTaskaux.split(/\n/g)[0];
      this.noTasks2 = noTaskaux.split(/\n/g)[1];
      this.title = k.find(f => f.name == "inbox").description[lang];
      this.selectTitle = k.find(f => f.name == "inbox_dialog_title").description[lang];
    });
  }

  getTasks(){
    this.taskService.getTasks(this.user_mail).subscribe(data => {
      this.tasks = data;
    });
  }

  getFolders(){
    this.folderService.getFolders(this.user_mail).subscribe(data => {
      this.folders = data;
    });
  }

  belongsHere(task:Task){
    if(task.parent_id == 0 && task.state == 'SEND' && task.dear_mail == this.user_mail){
      return true;
    }
    return false;
  }

  hasFromName(task:Task){
    if(task.from['name']){
      return true;
    }
    return false;
  }

  acceptTask(e,task){
    e.preventDefault();
    this.toselectTask = task;
    this.showSelectDialog = true;
  }

  rejectTask(e,task:Task){
    e.preventDefault();
    this.showSelectDialog = false;
    task.state = 'REJECTED';
    for (const i in this.tasks) {
      if(this.tasks[i].id == task.id){
        this.tasks.splice(+i,1);
        this.updateTask(task);
        break;
      }
    }
  }

  getSelectResponse(event){
    const res = event.response;
    const action = event.action;
    switch (action) {
      case "CONFIRM":
        this.showSelectDialog = false;
        this.toselectTask.folder = res;
        this.toselectTask.state = 'ACCEPTED';
        for (const i in this.tasks) {
          if(this.tasks[i].id == this.toselectTask.id){
            this.tasks.splice(+i,1);
            this.updateTask(this.toselectTask);
            break;
          }
        }
        break;      
      case "CANCEL":
        this.showSelectDialog = false;
        break;
    }    
  }

  updateTask(task){
    this.taskService.addTask(task).subscribe(res => {
      console.log(res);
    });
  }

  haveTasks(){
    let res = false;
    for(let i in this.tasks){
      if(this.belongsHere(this.tasks[i])){
        res = true;
        break;
      }
    }
    return res;
  }
}



