import { Component, OnInit } from '@angular/core';

import { AuthService } from '../auth.service';
import { CookieService } from 'angular2-cookie/core';
import { Router } from '@angular/router';

import { FirebaseService } from '../firebase.service';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs-compat';
import { ProfileService } from '../profile.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})

export class LoginComponent implements OnInit {
  log_data:any = undefined;
  titleTab1:string = undefined;
  titleTab2:string = undefined;
  phMail: string = undefined;
  phPass: string = undefined;
  phName: string = undefined;
  recoverPass: string = undefined;
  btnGoogle: string = undefined;
  signError1: string = undefined;
  signError2: string = undefined;
  signError3: string = undefined;
  signError4: string = undefined;
  signError5: string = undefined;
  signError6: string = undefined;
  info1: string = undefined;
  text_info1: string = undefined;
  text_info2: string = undefined;

  register:boolean = false;
  sendText = undefined;
  log:any = undefined;
  texts: Observable<any> = undefined;
  language: string = undefined;

  constructor(
    private authService: AuthService,
    private cookieService: CookieService,
    private router: Router,
    private firebaseService: FirebaseService,
    private firebaseDB: AngularFireDatabase,
    private ps: ProfileService,
  ) {
    this.language = navigator.language.split("-")[0] !== undefined ? navigator.language.split("-")[0] : "en";
   }

  ngOnInit() {
    this.getCokkie("accessToken");
    this.loadLanguage(this.language);
  }

  logIn(mail:any,pass:any): void{
    this.authService.logIn(mail,pass)
      .subscribe(log_data => {
        console.log(log_data);
        this.log_data = log_data;       
        this.setCookie();
      });
  }

  loadLanguage(lang: string){
    this.texts = this.firebaseDB.list('/text/list_text/').valueChanges();
    this.texts.subscribe(k => { 
      this.titleTab1 = k.find(f => f.name == "login_button_enter").description[lang];
      this.titleTab2 = k.find(f => f.name == "login_button_register").description[lang];
      this.phMail = k.find(f => f.name == "login_form_mail").description[lang];
      this.phPass = k.find(f => f.name == "login_form_pass").description[lang];
      this.phName = k.find(f => f.name == "login_form_name").description[lang];
      this.recoverPass = k.find(f => f.name == "login_footer_forgetpass").description[lang];
      this.sendText = k.find(f => f.name == "login_button_send").description[lang];
      this.btnGoogle = k.find(f => f.name == "login_button_google").description[lang];
      this.signError1 = k.find(f => f.name == "login_sign_error_1").description[lang];
      this.signError2 = k.find(f => f.name == "login_sign_error_2").description[lang];
      this.signError3 = k.find(f => f.name == "login_sign_error_3").description[lang];
      this.signError4 = k.find(f => f.name == "login_sign_error_4").description[lang];
      this.signError5 = k.find(f => f.name == "login_sign_error_5").description[lang];
      this.signError6 = k.find(f => f.name == "login_forget_errormessage").description[lang];
      this.info1 = k.find(f => f.name == "login_forget_sendmessage").description[lang];
      this.text_info1 = k.find(f => f.name == "login_info_1").description[lang];
      this.text_info2 = k.find(f => f.name == "login_info_2").description[lang];
    });
  }

  getCokkie(name){
    const accessToken = this.cookieService.get(name);
    console.log(accessToken);
  }

  setCookie(): void{
    if(this.log_data !== undefined){
      const data = this.log_data.user;
      if(data !== undefined){
        if (data.stsTokenManager) {
          localStorage.setItem("mail",data.email);
          localStorage.setItem("accessToken",data.stsTokenManager.accessToken);
          this.cookieService.put('accessToken', data.stsTokenManager.accessToken);

          if(data.lang === undefined) {
            data.lang = navigator.language.split("-")[0];
            localStorage.setItem("lang",data.lang);
            this.ps.updateProfile(data);
          }

          this.router.navigate(['']);    
          this.log = undefined;
        }else if(data.refreshToken) {
          localStorage.setItem("mail",data.email);
          localStorage.setItem("accessToken",data.uid);
          this.cookieService.put('accessToken', data.uid);

          if(data.lang === undefined) {
            data.lang = navigator.language.split("-")[0];
            localStorage.setItem("lang",data.lang);
            this.ps.updateProfile(data);
          }

          this.router.navigate(['']);    
          this.log = undefined;
        }else{        
          this.setLog(data.code);
        }
      } else {
        this.setLog(this.log_data.code);
      }      
    }    
  }

  setLog(code): void{
    switch (code) {
      case "auth/invalid-email":
        this.log = this.signError2;
        break;
      case "auth/wrong-password":
        this.log = this.signError5;
        break;
      case "auth/weak-password":
        this.log = this.signError1;
        break;
      case "auth/email-already-in-use":
        this.log = this.signError4;
        break;
      case "auth/recovery-true":
        this.log = this.info1
        break;
      case "auth/recovery-false":
        this.log = this.signError6;
        break;
      case "auth/user-not-found":
        this.log = this.signError3;
        break;
      default:
        // code...
        break;
    }
  }

  registerUser(mail,pass,name){
    this.authService.registerUser(mail,pass,name)
      .subscribe(data => {
        this.log_data = data;
        if(this.log_data.user.uid){
          this.setCookie();
        }else{
          this.setLog(data.code);
        }
      });
  }

  changeFormType(e){
    e.preventDefault();
    var element = e.target;
    if(!element.classList.contains('change-form')){
        element = element.parentNode;
    }
    if(!element.classList.contains('active')){ 
      if (this.register) {
        this.register = false;
        var loginbtn = document.getElementsByClassName('login-btn')[0];
        var registerbtn = document.getElementsByClassName('register-btn')[0];
        loginbtn.classList.add("active");
        registerbtn.classList.remove("active");
      }else{
        this.register = true;
        var loginbtn = document.getElementsByClassName('login-btn')[0];
        var registerbtn = document.getElementsByClassName('register-btn')[0];
        loginbtn.classList.remove("active");
        registerbtn.classList.add("active");
      }
    }
  }

  auth(e,mail,pass,name){    
    e.preventDefault();
    if(this.register){
      this.registerUser(mail,pass,name);
    }else{
      this.logIn(mail,pass);
    }
  }

  recoveryPassword(e){
    e.preventDefault();
    const mail = (<HTMLInputElement> document.getElementById("mail")).value;
    if(mail == '' || mail == undefined) {
      this.setLog("auth/invalid-email");
    }
    else {
      this.authService.recoveryPassWord(mail).subscribe(data => {
        if(data.complete){
          this.setLog("auth/recovery-true");
        }else{
          this.setLog("auth/recovery-false");
        }
      });
    }
  }

  socialSignIn(platform: string){
    this.firebaseService.socialAuth(platform).then(callback => {
      console.log("esto", callback);
      this.authService.socialUserRegister(callback).subscribe(f => {
        console.log("ok", f);
        if(f.complete){
          this.log_data = callback;       
          this.setCookie();    
        } else {
          console.log("something happened", f);
        }
      });
    }).catch(err => {
      console.log("NO OKEY: "+ err);
    });
  }

}



