import { Component, OnInit } from '@angular/core';
import { CookieService } from 'angular2-cookie/core';
import { Router } from '@angular/router';

import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs-compat';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  texts: Observable<any> = undefined;
  language: string = undefined;
  myWorks: string = undefined;
  inbox: string = undefined;
  follow: string = undefined;
  extentions: string = undefined;
  feedback: string = undefined;
  logout: string = undefined;

  constructor(
  	private cookieService: CookieService,
    private router: Router,
    private firebaseDB: AngularFireDatabase) {
      this.language = localStorage.getItem("lang");
     }

  ngOnInit() {
    this.texts = this.firebaseDB.list('/text/list_text/').valueChanges();
    this.texts.subscribe(k => { 
      this.myWorks = k.find(f => f.name == "myworks").description[this.language];
      this.inbox = k.find(f => f.name == "inbox").description[this.language];
      this.follow = k.find(f => f.name == "follow").description[this.language];
      this.extentions = k.find(f => f.name == "extensions").description[this.language];
      this.feedback = k.find(f => f.name == "feedback").description[this.language];
      this.logout = k.find(f => f.name == "logout").description[this.language];
    });
  }

  closeSession(): void{
  	this.cookieService.removeAll();
  	location.reload();
  }

}



