import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Observable } from 'rxjs-compat';
import { FirebaseService } from '../../firebase.service';
import { AngularFireDatabase } from 'angularfire2/database';

@Component({
  selector: 'app-select-dialog',
  templateUrl: './select-dialog.component.html',
  styleUrls: ['./select-dialog.component.scss']
})
export class SelectDialogComponent implements OnInit {
  @Input() title: string;
  @Input() options;
  selected;
  btnAccept: string = undefined;
  btnCancel: string = undefined;

  texts: Observable<any> = undefined;
  language: string = undefined;

  @Output() response = new EventEmitter();

  constructor(
    private firebaseService: FirebaseService,
    private firebaseDB: AngularFireDatabase) {
      this.language = localStorage.getItem("lang");
     }

  ngOnInit() {
    this.setSelected();
    this.loadLanguage(this.language);
  }

  loadLanguage(lang: string){
    this.texts = this.firebaseDB.list('/text/list_text/').valueChanges();
    this.texts.subscribe(k => { 
      this.btnAccept = k.find(f => f.name == "inbox_icon_accept").description[lang];
      this.btnCancel = k.find(f => f.name == "inbox_icon_cancel").description[lang];
    });
  }

  sendResponse(action){
  	this.response.emit({
  		response: this.selected,
      action: action
  	});
  }

  setSelected(){
    for (const i in this.options) {
       if(this.options[i].selected){
         this.selected = this.options[i].name;
         break;
       }
    }
  }

}



