import { Component, OnInit } from '@angular/core';
import { ProfileService } from '../profile.service';
import { Profile } from '../Models/profile';
import { LogService } from '../log.service';
import { Observable } from 'rxjs-compat';
import { AngularFireDatabase } from 'angularfire2/database';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
	profile: Profile;
	selectedImg: any;
  mail: string;
  title: string = undefined;
  btnSave: string = undefined;
  profile_updated: string = undefined;
  
  texts: Observable<any> = undefined;
  language: string = undefined;
  
  constructor(
    private pf: ProfileService,
    private ls: LogService,
    private firebaseDB: AngularFireDatabase
  ){
    this.mail = localStorage.getItem("mail");
    this.language = localStorage.getItem("lang");
  	pf.getProfile(this.mail).subscribe(data => {
  		this.profile = data;
  	});
  }

  ngOnInit() {
    this.loadLanguage(this.language);
  }

  loadLanguage(lang: string){
    this.texts = this.firebaseDB.list('/text/list_text/').valueChanges();
    this.texts.subscribe(k => { 
      this.title = k.find(f => f.name == "myprofile").description[lang];
      this.btnSave = k.find(f => f.name == "myprofile_save").description[lang];
      this.profile_updated = k.find(f => f.name == "myprofile_save_data").description[lang];
    });
  }

  saveProfile(){
  	this.pf.updateProfile(this.profile).subscribe(response => {
      this.ls.emitChange(this.profile_updated);
    });
  }

  changeImg(e){
  	const file = e.target.files[0];
  	var reader = new FileReader();
    reader.onload = this.fileOnload;
    reader.readAsDataURL(file);
  }

  fileOnload(e) {
		var result=e.target.result;
		document.getElementsByClassName("profileImg")[0].setAttribute("src",result);
	}

}



